.Navbar_btn__2IAAD {
    background: none;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
    transition: 1.4s;
    position: relative;
    overflow: hidden;
    width: 100px;
    height: 30px;
    margin: 5px;
    border-radius: 5px;
    border:1px solid #b99af5;

}

.Navbar_btn__2IAAD:hover {
    transition: 1.4s;
}

.Navbar_btn__2IAAD::before {
    transition: 1.4s;
    content: "";
    position: absolute;
    left: 0;
    width: 120%;
    height: 0%;
    background: #b99af5;
    z-index: -1;
    top: 0;
    border-radius: 0 0 50% 50%;
}

.Navbar_btn__2IAAD:hover::before {
    height: 200%;
    transition: 1.4s;
}
.Navbar_btn__2IAAD:hover:before {
    bottom: auto;
    top: 0;
    width: 100%;
    transition: 1.4s;
}

