@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

.app-wrapper {
    display: grid;
    max-width: 1200px;
    margin: 0 auto;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 60px 1fr;
    grid-gap: 0px;
    grid-template-areas:
    "h h "
    "n c ";
}