.nav {
    grid-area: n;
    background: rgba(185, 154, 245, 0.48);
    border: 2px solid #000;
    height: 400px;
}
.active {
    color: #b99af5;
}
.item,.item1 {
    columns: #fff;
    margin: 10px;
    color: rgba(0, 0, 0, 0.48);
    transition: 1s;
}
.item, .item> span {
    position: relative;
    color: inherit;
    text-decoration: none;
    line-height: 24px;
  }
.item:before, .item:after, .item > .item:before, .item > .item:after {
    content: '';
    position: absolute;
    transition: transform .5s ease;
  }
.item {
    padding-top: 10px;
  }
.item:before {
    left: 0;
    bottom: 0;
    width: 50px;
    height: 2px;
    background: #492059;
    transform: scaleX(0);
  }
.item:hover:before {
    transform: scaleX(1);
  }

.item1, .item1> span {
    position: relative;
    color: inherit;
    text-decoration: none;
    line-height: 24px;
}
.item1:before, .item1:after, .item1 > .item1:before, .item1 > .item1:after {
    content: '';
    position: absolute;
    transition: transform .5s ease;
}
.item1 {
    padding-top: 10px;
}
.item1:before {
    left: 0;
    bottom: 0;
    width: 66px;
    height: 2px;
    background: #492059;
    transform: scaleX(0);
}
.item1:hover:before {
    transform: scaleX(1);
}

.btn {
    background: none;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
    transition: 1.4s;
    position: relative;
    overflow: hidden;
    width: 100px;
    height: 30px;
    margin: 5px;
    border-radius: 5px;
    border:1px solid #b99af5;

}

.btn:hover {
    transition: 1.4s;
}

.btn::before {
    transition: 1.4s;
    content: "";
    position: absolute;
    left: 0;
    width: 120%;
    height: 0%;
    background: #b99af5;
    z-index: -1;
    top: 0;
    border-radius: 0 0 50% 50%;
}

.btn:hover::before {
    height: 200%;
    transition: 1.4s;
}
.btn:hover:before {
    bottom: auto;
    top: 0;
    width: 100%;
    transition: 1.4s;
}
